import {
	uniIcons
} from '@dcloudio/uni-ui'
export default {
	components: {
		uniIcons
	},
	data() {
		return {

			openInput: "您好，我现在正在忙，您有什么事情可以简要的说明一下。",
			recStatus: "按住我开始录音",
			recComplete: false,
			isleft: true,
			isRight: false,
			progerssShow: false,
			recordTipShow: {
				opacity: "1"
			},
			isRec: false,
			isSave: false,
			isPlay: true,
			confirmShow: false,
			failShow: false
		}
	},
	methods: {
		xiaomishuo() {
				this.isleft = true,
				this.isRight = false,
				this.openInput = "您好，我现在正在忙，您有什么事情可以简要的说明一下。"
		},
		gotoSelfRecord() {
				this.btnShow = false,
				this.isleft = false,
				this.isRight = true,
				this.openInput = "在这里录下你想传达的留言"
		},
		startRec() {
			this.progerssShow = true,
				this.recordTipShow.opacity = "0",
				this.isRec = true,
				this.recStatus = "放开我停止录音",
				this.isPlay = true

		},
		stopRec() {
			this.progerssShow = false,
				this.recordTipShow.opacity = "1",
				this.isRec = false,
				this.recStatus = "按住我开始录音",
				this.isPlay = false
			if (true) {
				this.isSave = true,
				this.recComplete = true
			}
		},
		delRec() {
			this.confirmShow = true
		},
		confirmSave(){
			this.failShow = true
		},
		cancelSave() {
			this.confirmShow = false,
			this.failShow = false
		},
		// 返回上一页
		goback(){
			uni.navigateBack({
				delta:1
			})
		}
	},
}
